@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html,
body {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
}

button {
  font: inherit;
  background: #ff2058;
  padding: 0.5rem 2rem;
  color: white;
  border: 1px solid #ff2058;
  margin: 0.5rem 0;
  border-radius: 5px;
  cursor: pointer;
}

button:hover,
button:active {
  background: white;
  color: #ff2058;
}

button:focus {
  outline: none;
}

button.button-outline {
  background: white;
  color: #ff2058;
}

button.button-outline:hover,
button.button-outline:active {
  background: #ff2058;
  color: white;
  box-shadow: none;
}

.main-header {
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ff2058;
  }
  
  .main-header nav {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .main-header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .main-header li {
    padding: 0;
    margin: 0 2rem;
  }
  
  .main-header a {
    text-decoration: none;
    color: white;
    font-weight: bold;
  }
  
  .main-header a:hover,
  .main-header a:active,
  .main-header a.active {
    color: #560061;
  }
  
.card {
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  }
  
.product-item h2 {
    font-size: 1.15rem;
    margin: 0.25rem 0;
  }
  
  .product-item h2.is-fav {
    color: #ff2058;
  }
  
  .product-item p {
    margin: 0.25rem 0;
  }
  
.products-list {
    list-style: none;
    margin: 3rem;
    padding: 0;
  }
  
  .placeholder {
    margin: 4rem;
    text-align: center;
  }
  
.favorite-item h2 {
    font-size: 1.15rem;
    margin: 0.25rem 0;
  }
  
  .favorite-item p {
    margin: 0.25rem 0;
  }
  
